import React, { useEffect, useRef, useState } from 'react';

// style
import styled from "styled-components"
import styles from './purchase.module.css';

// components
import CheckBox from '../../components/common/checkbox';
import Header from '../../components/header/header';
import PurchaseInput from '../../components/purchase/purchaseInput';
import PurchaseInputTitle from '../../components/purchase/purchaseInputTitle';
import PurchaseListItem from '../../components/purchase/purchaseListItem';
import InsertComma from '../../components/common/insertComma';

// img
import BillElTitle from '../../components/purchase/billElTitle';
import BillElDetail from '../../components/purchase/billElDetail';

import {
    Media,
    Width,
    FontSize,
    Margin,
    PT,
    MB,
    Padding,
    MT,
    Padding2,
    Height,
    ML,
    MR
} from '../../components/common/mediaStlye';
import ServiceModal from '../../components/common/modal/serviceModal';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';


//  
const zipErr = "   .  .";
const addressErr = "   .  ."
const phoneErr = "   ."
const emailErr = "   .  ."
const pwErr = "  ."
const couponErr = "  .";
const pointErr = " . ";

//  
const emailDesc = "(   .     .)";
const pwDesc = "( . 4 12     )";

// Input  
const inputTitleData = {
    personal:{title:"Personal Information", id:"PersonalCkBox", text:"",},
    delivery:{title:"Delivery Information", id:"DeliveryCkBox", text:"",},
}

//  
const payData = ["", " ", " ", ""];
    
const mediaPxMb = 1024;
const mbSize = 428;



const Purchase = ({status}) => {
    const history = useHistory();
    const selectPrdData = useSelector(store=>{return store.selectPrdData;});
    const [prdList, setPrdList] = useState([]);
    const [couponData, setCouponData] = useState([]);
    const [isCondition, setIsCondition] = useState(false);
    const [isPrivacy, setIsPrivacy] = useState(false);

    //  
    const [priceData, setPriceData] = useState({
        prdPrice:0, // 
        delivery:0, // 
        localDelivery:0, // 
        prdSale:0, //  
        orderSale:0, //  
        deliverySale:0, //  
        timeSale:0, //  
        usePoint:0, //  
        vat:0, //  
        prdPoint:0, //  
        userPoint:0, //  
        couponPoint:0, //  
    });
    const [totalOrderPrice, setTotalOrderPrice] = useState(0);
    const [totalSalePrice, setTotalSalePrice] = useState(0);
    const [totalPointPrice, setTotalPointPrice] = useState(0);
    const [totalPrice, setTotalPrice] = useState(0)

    const [isDetail, setIsDetail] = useState([false, false, false]);

    const [policyStatus, setPolicyStatus] = useState([false, false, false]);

    // Personal Information
    const perNameRef = useRef();
    const perZipRef = useRef();
    const perAddZipRef = useRef();
    const perAddMoreZipRef = useRef();
    const perPhoneZipRef = useRef();
    const perEmailZipRef = useRef();
    const perPwZipRef = useRef();
    const perPwMoreZipRef = useRef();
    // Delivery Information
    const delNameRef = useRef();
    const delZipRef = useRef();
    const delAddRef = useRef();
    const delPhoneRef = useRef();
    const delMsgRef = useRef();
    // Coupon & Point
    const couponRef = useRef();
    const pointRef = useRef();

    // Personal Information Input  
    const [inputData, setInputData] = useState([
        {title:"", ref: perNameRef, isError:false},
        {title:"", ref: perZipRef, isError:false, errorText:zipErr},
        {title:" ", ref: perAddZipRef, isError:false, errorText:addressErr, isSearch:true},
        {title:" ", ref: perAddMoreZipRef, isError:false, errorText:addressErr, isNotImp:true},
        {title:"", ref: perPhoneZipRef, isError:false, errorText:phoneErr},
        {title:"", ref: perEmailZipRef, subText:emailDesc, isError:false, errorText:emailErr},
        {title:"", ref: perPwZipRef, subText:pwDesc, isError:false},
        {title:" ", ref: perPwMoreZipRef, isError:false, errorText:pwErr},
    ])

    // Personal Information Input  
    const [inputDataDel, setInputDataDel] = useState([
        {title:"", ref: delNameRef, isError:false},
        {title:"", ref: delZipRef, isError:false, errorText:zipErr},
        {title:" ", ref: delAddRef, isError:false, errorText:addressErr, isSearch:true},
        {title:"", ref: delPhoneRef, isError:false, errorText:phoneErr},
        {title:" ", ref: delMsgRef, isError:false, errorText:phoneErr, isNotImp:true},
    ])

    // &
    useEffect(() => {
        //    .
        setCouponData([
            {title:"", ref: couponRef, isError:false, errorText:couponErr, isNotImp:true},
            {title:"", ref: pointRef, isError:false, subText:`(   : 12,300 won )`, errorText:pointErr, isNotImp:true},
        ])
    }, [])

    //  , , 
    useEffect(() => {
        let e=priceData;
        setTotalOrderPrice(e.prdPrice + e.delivery + e.localDelivery);
        setTotalSalePrice(e.prdSale + e.orderSale + e.deliverySale + e.timeSale +e.usePoint);
        setTotalPointPrice(e.prdPoint + e.userPoint + e.couponPoint);
    }, [priceData])

    //   
    useEffect(() => {
        setTotalPrice(totalOrderPrice + totalSalePrice + totalPointPrice + priceData.vat);
    }, [totalOrderPrice, totalSalePrice, totalPointPrice, priceData.vat])


    //  
    const initError = (state, setState) => {
        let newArr = state;
        for(let i = 0 ; i < newArr.length ; i++ ){
            newArr[i].isError = false;
        }
        setState([...newArr]);
    }

    //  
    const errorPop = (index, state, setState) => {
        let newArr = state;
        newArr[index].isError = true;
        setState([...newArr]);
    }

    //   
    const onClickPurchase = () => {
        initError(inputData, setInputData);
        initError(inputDataDel, setInputDataDel);
        initError(couponData, setCouponData);
        if(!perNameRef.current.value){errorPop(0, inputData, setInputData)};
        if(!perZipRef.current.value){errorPop(1, inputData, setInputData)};
        if(!perAddZipRef.current.value){errorPop(2, inputData, setInputData)};
        if(!perAddMoreZipRef.current.value){errorPop(3, inputData, setInputData)};
        if(!perPhoneZipRef.current.value){errorPop(4, inputData, setInputData)};
        if(!perEmailZipRef.current.value){errorPop(5, inputData, setInputData)};
        if(!perPwZipRef.current.value){errorPop(6, inputData, setInputData)};
        if(!perPwMoreZipRef.current.value){errorPop(7, inputData, setInputData)};
        
        if(!delNameRef.current.value){errorPop(0, inputDataDel, setInputDataDel)};
        if(!delZipRef.current.value){errorPop(1, inputDataDel, setInputDataDel)};
        if(!delAddRef.current.value){errorPop(2, inputDataDel, setInputDataDel)};
        if(!delPhoneRef.current.value){errorPop(3, inputDataDel, setInputDataDel)};
        if(!delMsgRef.current.value){errorPop(4, inputDataDel, setInputDataDel)};

        if(!couponRef.current.value){errorPop(0, couponData, setCouponData)};
        if(!pointRef.current.value){errorPop(1, couponData, setCouponData)};


    }

    //    
    const onClickPerCkBox = () => {
        console.log("onClickPerCkBox");
    }

    //   
    const onClickDelCkBox = () => {
        console.log("onClickDelCkBox");
    }

    //   
    const onClickPriceToggle = (index) => {
        let newArr = isDetail;
        newArr[index] = !newArr[index];
        setIsDetail([...newArr]);
    }

    //   
    const onChangePolicy = (index) => {
        let newArr = policyStatus;
        if(index == 0){
            if(!newArr[0]){
                newArr = [true, true, true];
            }else{
                newArr = [false, false, false];
            }
            setPolicyStatus([...newArr]);
            return;
        }
        newArr[index] = !newArr[index];
        if(newArr[1] && newArr[2]){
            newArr[0] = true;
        }else{
            newArr[0] = false;
        }
        setPolicyStatus([...newArr]);
    }
    
    //   redux 
    useEffect(() => {
        //    (ex))  
        if(selectPrdData.selectData.length==0){
            history.push("/");
            return;
        }
        const cartData = selectPrdData.selectData[0].cart_items.data;
        setPrdList([...cartData]);
        let priceText = 0;
        let newPriceObj = priceData;

        //   
        for(let i = 0 ; i < cartData.length ; i++){
            priceText += cartData[i].prd_price * cartData[i].count
        }
        newPriceObj.prdPrice = priceText;
        setPriceData({...newPriceObj});
    }, [])


    return(
        <Container>
            <Header />
            {/*  */}
            {isCondition&&<ServiceModal stasus={0} onClickBack={() => setIsCondition(false)}/>}
            {/*  */}
            {isPrivacy&&<ServiceModal stasus={1} onClickBack={() => setIsPrivacy(false)}/>}
            <GridWrap>
                <Grid>
                    {/*   */}
                    <Table>
                        <TableTitleWrap>
                            <TableTitleGrid active={status=="slowOrder"}>
                                {
                                    status=="slowOrder"&&
                                    <TableTitle active={status=="slowOrder"}>Date</TableTitle>
                                }
                                <TableTitle active={status=="slowOrder"}>Option</TableTitle>
                                <TableTitle active={status=="slowOrder"}>Quantity</TableTitle>
                            </TableTitleGrid>
                        </TableTitleWrap>
                        
                        <PrdListWrap>
                            {
                                prdList.map((item, index) => {
                                    return(
                                        <PurchaseListItem slowOrder={status=="slowOrder"?true:false} id={index} item={item} key={index}/>
                                    )
                                })
                            }
                        </PrdListWrap>
                    </Table>

                    {/*   */}
                    <PersonalWrap>
                        <PurchaseInputTitle data={inputTitleData.personal} onClick={onClickPerCkBox}/>
                        {
                            inputData.map((item, index) => {
                                return(
                                    <PurchaseInput item={item} key={index}/>
                                )
                            })
                        }
                    </PersonalWrap>

                    {/*   */}
                    <DeliveryWrap>
                        <PurchaseInputTitle data={inputTitleData.delivery} onClick={onClickDelCkBox}/>
                        {
                            inputDataDel.map((item, index) => {
                                return(
                                    <PurchaseInput item={item} key={index}/>
                                )
                            })
                        }
                    </DeliveryWrap>

                    {/*  */}
                    <PolictWrap>
                        <PolicyTitle>Policy Agreement</PolicyTitle>
                        <PolicyCkBoxWrap>
                            <AllCheckWrap>
                                <CheckBoxWrap>
                                    <CheckBox checked={policyStatus[0]} onChange={() => onChangePolicy(0)} id="allcheck" red/>
                                    <CheckText></CheckText>
                                </CheckBoxWrap>
                            </AllCheckWrap>

                            <CheckBoxWrap>
                                <CheckBox checked={policyStatus[1]} onChange={() => onChangePolicy(1)} id="agreePolicy"/>
                                <CheckText>
                                       
                                    <WatchContent onClick={() => setIsCondition(true)}>  ()</WatchContent>
                                </CheckText>
                            </CheckBoxWrap>

                            <CheckBoxWrap>
                                <CheckBox checked={policyStatus[2]} onChange={() => onChangePolicy(2)} id="agreePersonal"/>
                                <CheckText>
                                        
                                    <WatchContent onClick={() => setIsPrivacy(true)}> *  ()</WatchContent>
                                </CheckText>
                            </CheckBoxWrap>
                        </PolicyCkBoxWrap>

                    </PolictWrap>

                    {/*  */}
                    <PayWrap>
                        <PayTitle>Payment method</PayTitle>
                        <InputList>
                            {
                                payData.map((item, index) => {
                                    return(
                                        <InputWrap key={index}>
                                            <PayInput className={styles.radioInput} type="radio" id={`${index}radio`} name="pay" defaultChecked={index==0}/>
                                            <PayText for={`${index}radio`} name="pay" className={styles.radioLabel}></PayText>
                                            <PayTextRadio>{item}</PayTextRadio>
                                        </InputWrap>

                                    )
                                })
                            }
                        </InputList>
                    </PayWrap>

                    {/* ,  */}
                    <CounponWrap>
                        <CouponTitle>Coupon &#38; Points</CouponTitle>
                        <CounponInputWrap>
                            {
                                couponData.map((item, index) => {
                                    return(
                                        <PurchaseInput item={item} key={index} marginRight={index==0}/>
                                    )
                                })
                            }
                        </CounponInputWrap>
                    </CounponWrap>

                    {/*   */}
                    <BillWrap>
                        <BillList>
                            {/*     */}
                            <BillElTitle title={" "} price={totalOrderPrice} onClick={() => onClickPriceToggle(0)} isDetail={isDetail[0]}/>
                            {
                                isDetail[0]&&
                                <>
                                    <BillElDetail title={""} price={priceData.prdPrice}/>
                                    <BillElDetail title={""} price={priceData.delivery}/>
                                    <BillElDetail title={""} price={priceData.localDelivery}/>
                                </>
                            }
                            {/*   */}
                            <BillElTitle title={" "} price={totalSalePrice} onClick={() => onClickPriceToggle(1)} isDetail={isDetail[1]}/>
                            {
                                isDetail[1]&&
                                <>
                                    <BillElDetail title={" "} price={priceData.prdSale}/>
                                    <BillElDetail title={" "} price={priceData.orderSale}/>
                                    <BillElDetail title={" "} price={priceData.deliverySale}/>
                                    <BillElDetail title={" "} price={priceData.timeSale}/>
                                    <BillElDetail title={" "} price={priceData.usePoint}/>
                                </>
                            }

                            {/*    */}
                            <BillElTitle title={"  "} noArrow price={priceData.vat}/>

                            {/*   */}
                            <BillElTitle title={" "} price={totalPointPrice} onClick={() => onClickPriceToggle(2)} isDetail={isDetail[2]}/>
                            {
                                isDetail[2]&&
                                <>
                                    <BillElDetail title={" "} price={priceData.prdPoint}/>
                                    <BillElDetail title={" "} price={priceData.userPoint}/>
                                    <BillElDetail title={" "} price={priceData.couponPoint}/>
                                </>
                            }
                        </BillList>


                        <TotalPriceWrap>
                            <PrePrice>{InsertComma("???")} won</PrePrice>
                            <CurPriceWrap>
                                <TotalText>Total.</TotalText>
                                <TotalPrice>{InsertComma(totalPrice)} won</TotalPrice>
                            </CurPriceWrap>
                        </TotalPriceWrap>
                    </BillWrap>
                    
                    {/*   */}
                    <PurchaseBtnWrap>
                        <PurchaseBtn onClick={() => onClickPurchase()}></PurchaseBtn>
                    </PurchaseBtnWrap>
                </Grid>
            </GridWrap>
        </Container>
    )
};

export default Purchase;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100vw - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
        PT(74, mbSize),
    ])}
`
const Grid = styled.div`
    width:854px;
    margin:0 auto;
    ${Media(mediaPxMb,[
        "width:100vw",
        "box-sizing: border-box",
    ])}
`
const Table = styled.div`
    padding: 92px 0 62px;
    ${Media(mediaPxMb,[
        Padding(30, 0, 57, 0, mbSize),
    ])}
`
const TableTitleWrap = styled.div`
    display: flex;
    justify-content: flex-end;
    margin-bottom: 14px;
    ${Media(mediaPxMb,[
        MB(14, mbSize),
    ])}
`
const TableTitleGrid = styled.div`
    display: flex;
    justify-content: space-between;
    ${({active}) => {
        return active?
        `width: 525px;`
        :
        `width: 200px;`
    }}
    @media (max-width: ${mediaPxMb}px) {
        ${({active}) => {
        return active?
        `
        ${Width(191, mbSize)}
        ${MR(25, mbSize)}
        `
        :
        `
        ${Width(105, mbSize)}
        ${MR(25, mbSize)}
        `
        }}
    }
`
const TableTitle = styled.div`
    font-size: 15px; 
    text-align: center;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
    ${({active}) => {
        return active?
        `
        width: calc(100% / 3);
        `
        :
        `width: 50%;`
    }}
`
const PrdListWrap = styled.div`
    border-top: 6px solid #000;
    padding: 2px 0 0 31px;
    ${Media(mediaPxMb,[
        Padding(0, 25, 0, 25, mbSize)
    ])}
`
const PersonalWrap = styled.div`
    ${Media(mediaPxMb,[
        "box-sizing: border-box",
        Padding2(0, 22, mbSize),
    ])}
`
const DeliveryWrap = styled.div`
    margin-top: 58px;
    ${Media(mediaPxMb,[
        "box-sizing: border-box",
        Padding2(0, 22, mbSize),
        MT(34, mbSize),
    ])}
`
const PurchaseBtnWrap = styled.div`
    width: 100%;
    margin: 91px 0 259px;
    text-align: center;
    ${Media(mediaPxMb,[
        Margin(58, 0, 203, 0, mbSize),
    ])}
`
const PurchaseBtn = styled.button`
    width: 385px;
    height: 66px;
    border:1px solid #000;
    ${Media(mediaPxMb,[
        Width(385, mbSize),
        Height(66, mbSize),
    ])}
`
const CheckBoxWrap = styled.div`
    display: flex;
    align-items: center;
    width: 500px;
    margin-bottom: 15px;
    &:first-child{
        margin-bottom: 0px;
    }
    ${Media(mediaPxMb,[
        "width:100%",
        "box-sizing: border-box",
        Padding2(0, 22, mbSize),
        MB(9, mbSize), 
    ])}
`
const CheckText = styled.div`
    font-size: 15;
    font-weight: 400;
    font-family: "HelveticaNeue";
    margin-left: 13px;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
`
const WatchContent = styled.span`
    font-size: 15;
    font-weight: 400;
    font-family: "HelveticaNeue";
    cursor: pointer;
`
const PolictWrap = styled.div`
    margin-top: 54px;
`
const PolicyTitle = styled.div`
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 26px;
    ${Media(mediaPxMb,[
        "box-sizing: border-box",
        Padding2(0, 22, mbSize),
    ])}
`
const PolicyCkBoxWrap = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`
const AllCheckWrap = styled.div`
    width: 100%;
    background-color: #FBFBFB;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 44px;
    margin-bottom: 12px;
`
// 
const PayWrap = styled.div`
`
const PayTitle = styled.div`
    margin: 71px 0 31px;
    font-size: 18px;
    font-weight: 500;
    ${Media(mediaPxMb,[
        "box-sizing: border-box",
        Padding2(0, 24, mbSize),
    ])}
`
const InputList = styled.ul`
    display: flex;
    ${Media(mediaPxMb,[
        "flex-wrap: wrap",
        "box-sizing: border-box",
        Padding2(0, 24, mbSize),
    ])}
`
const InputWrap = styled.li`
    display: flex;
    align-items: center;
    ${Media(mediaPxMb,[
        "width: calc(100%/3)",
        Margin(0, 0, 27, 0, mbSize)
    ])}
`
const PayInput = styled.input`
`
const PayText = styled.label`
`
const PayTextRadio = styled.span`
    font-size: 15px;
    font-weight: 500;
    margin: 0 40px 0 16px;
    border: 0;
    ${Media(mediaPxMb,[
        "margin:0",
        ML(13, mbSize),
        FontSize(15, mbSize),
    ])}
`
// 
const CounponWrap = styled.div`
`
const CouponTitle = styled.div`
    margin: 69px 0 24px;
    font-size: 18px;
    font-weight: 500;
    ${Media(mediaPxMb,[
       Margin(19, 0, 13, 0, mbSize),
       FontSize(18, mbSize), 
       "box-sizing: border-box",
        Padding2(0, 24, mbSize),
    ])}
`
const CounponInputWrap = styled.div`
    display: flex;
    ${Media(mediaPxMb,[
        "flex-direction: column",
       "box-sizing: border-box",
        Padding2(0, 24, mbSize),
    ])}
`

//   
const BillWrap = styled.div`
    border-top: 1px solid #707070;
    margin-top: 61px;
    padding-top: 26px;
    ${Media(mediaPxMb,[
        MT(43, mbSize),
        PT(12, mbSize)
    ])} 
`
const BillList = styled.div`
    display: flex;
    flex-direction: column;
    padding: 0 52px 0 30px;
    ${Media(mediaPxMb,[
       "box-sizing: border-box",
        Padding2(0, 37, mbSize),
    ])}
`
const BillListEl = styled.div`
`
const TotalPriceWrap = styled.div`
    background-color: #F8F7F7;
    width: 100%;
    display: flex;
    height: 76px;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    margin-top: 46px;
    padding-right: 56px;
    ${Media(mediaPxMb,[
       "box-sizing: border-box",
        Padding2(0, 22, mbSize), 
    ])}
`
const PrePrice = styled.del`
    font-size: 12px;
    font-weight: 500;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize)
    ])}
`
const CurPriceWrap = styled.div`
    display: flex;
`
const TotalText = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize)
    ])}
`
const TotalPrice = styled.div`
    margin-left: 56px;
    font-size: 18px;
    font-weight: 500;
    ${Media(mediaPxMb,[
        ML(56, mbSize),
        FontSize(18, mbSize),
    ])}
`