//react
import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"

// img 
import instaWhiteImg from '../../../img/common/instaWhite.png';
import lockWhiteImg from '../../../img/common/lockWhite.png';

import {
    Media,
    Width,
    Padding,
    FontSize,
    Height,
    MB,
    Px,
} from '../../common/mediaStlye.js';
import ModalHead from './modalHead';
import PolicyAlert from './policyAlert';

const mediaPxMb = 1024;
const mbSize = 428;

const ServiceModal = ({stasus, onClickBack})=> {


    const [subTitle, setSubTitle] = useState("");
    const [title, setTitle] = useState("");
    const mapData = [
        {title:"Login", subTitle:["/ ", "", ""]},
        {title:"Inside", subTitle:["Brand", "Slow order"]},
        {title:"Collection", subTitle:["Episodes", "Products"]},
        {title:"Taste", subTitle:["Artist", "M.Seulmit"]},
        {title:"Info", subTitle:["Notice", "Q&A", "Review"]},
    ]


    const contentReturn = (int) => {
        // 
        if(int == 0){
            return(
                <Content>
                     1  ()<br />

                      ( ,  )   
                    ( )   ( )    , 
                         .
                     2  ()


                         (  )     
                                
                      .<br />
                                 
                     .<br />
                             4   
                            .<br />
                            ,    
                     ,         
                    .<br />
                                
                    .
                     3  (     )


                            ,   (  
                         ), , , ,  , 
                    ,           
                     . ,           
                    .<br />
                               , <br />
                     1  ()<br />

                      ( ,  )   
                    ( )   ( )    , 
                         .
                     2  ()


                         (  )     
                                
                      .<br />
                                 
                     .<br />
                             4   
                            .<br />
                            ,    
                     ,         
                    .<br />
                                
                    .
                     3  (     )


                            ,   (  
                         ), , , ,  , 
                    ,           
                     . ,           
                    .<br />
                               , <br />
                </Content>
            )
        } // 
        else if(int == 1){
            return(
                <Content>
                     1  ()<br />

                      ( ,  )   
                    ( )   ( )    , 
                         .
                     2  ()


                         (  )     
                                
                      .<br />
                                 
                     .<br />
                             4   
                            .<br />
                            ,    
                     ,         
                    .<br />
                                
                    .
                     3  (     )


                            ,   (  
                         ), , , ,  , 
                    ,           
                     . ,           
                    .<br />
                               , <br />
                     1  ()<br />

                      ( ,  )   
                    ( )   ( )    , 
                         .
                     2  ()


                         (  )     
                                
                      .<br />
                                 
                     .<br />
                             4   
                            .<br />
                            ,    
                     ,         
                    .<br />
                                
                    .
                     3  (     )


                            ,   (  
                         ), , , ,  , 
                    ,           
                     . ,           
                    .<br />
                               , <br />
                </Content>
            )
        } // 
        else if(int == 2){
            return(
                <ContentMap>
                    <List>
                        {
                            mapData.map((item, index) => {
                                return(
                                    <ListEl key={index}>
                                        <MapTitle>{item.title}</MapTitle>
                                        {
                                            item.subTitle.map((item2, index2) => {
                                                return(
                                                    <SubTitle key={index2}>{item2}</SubTitle>
                                                )
                                            })
                                        }
                                    </ListEl>
                                )
                            })
                        }
                    </List>
                    <IconWrap>
                        <Icon src={lockWhiteImg} alt=" "/>
                        <Icon src={instaWhiteImg} alt=" "/>
                    </IconWrap>
                </ContentMap>
            )
        }
    }

    useEffect(() => {
        if(stasus == 0){
            setSubTitle("");
            setTitle("Terms and Conditions");
            return;
        }else if(stasus == 1){
            setSubTitle("");
            setTitle("Privacy Terms");
            return;
        }else if(stasus == 2){
            setSubTitle("");
            setTitle("Site map");
            return;
        } 
    }, [stasus])

    return(
        <Container>
            <ReviewModalWrap>
                <ModalHead subtitle={subTitle} title={title} onClickBack={onClickBack}/>
                {contentReturn(stasus)}
            </ReviewModalWrap>
        </Container>
    )   
};

export default ServiceModal;

const Container = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.47);
    z-index: 99;
    top:0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    ${Media(mediaPxMb,[
        "background: rgba(0, 0, 0, 0.8)"
    ])}
`
const ReviewModalWrap = styled.div`
    background: #fff;
    padding: 25px 35px 45px;
    width: 640px;
    box-sizing: border-box;
    position: relative;
    ${Media(mediaPxMb,[ 
        `max-height: calc(100vh - ${Px(100, mbSize)})`,
        "overflow: scroll",
        Width(406, mbSize),
        Padding(28, 8, 36, 8, mbSize)
    ])}
`
const Content = styled.div`
    font-size: 15px;
    height: 603px;
    overflow: scroll;
    box-sizing: border-box;
    margin: 21px 21px 0;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        Height(523, mbSize)
    ])}
`
const ContentMap = styled.div`
    font-size: 15px;
    box-sizing: border-box;
    margin: 21px 21px 0;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        Height(523, mbSize)
    ])}
`
const List = styled.ul`
`
const ListEl = styled.li`
    margin-bottom: 18px;
    ${Media(mediaPxMb,[
        MB(18, mbSize)
    ])}
`
const MapTitle = styled.div`
    font-size: 18px;
    color:#211013;
    font-weight: 500;
    ${Media(mediaPxMb,[
        FontSize(18, mbSize)
    ])}
`
const SubTitle = styled.div`
    font-size: 15px;
    color: #707070;
    font-weight: 500;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize)
    ])}
`
const IconWrap = styled.div`
    margin-top: 12px;
    display: flex;
    flex-direction: column;
    ${Media(mediaPxMb,[
        "align-items: flex-end",
        "justify-content: flex-end",
        "flex-direction: row"
    ])}
`
const Icon = styled.img`
    width: 25px;
    margin-bottom: 18px;
    filter: invert();
    cursor: pointer;
    &:last-child{
        ${Media(mediaPxMb,[
            Height(25, mbSize),
        ])}
    }
    ${Media(mediaPxMb,[
        Width(25, mbSize),
        `margin:0 0 0 ${Px(18, mbSize)}`,
    ])}
`