//react
import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"

// img 
import closeIconImg from '../../../img/common/closeIcon.png';

import {
    Media,
    Width,
    FontSize,
    Padding,
    MT,
    Height,
    PL,
    Top,
    Right,
    LineH,
    MB,
    Px,
} from '../../common/mediaStlye.js';

const mediaPxMb = 1024;
const mbSize = 428;

const PolicyAlert = ({onClickClose}) => {

    return(
        <Container>
            <ReviewModalWrap>
                <CloseBtn onClick={() => onClickClose()} src={closeIconImg} alt=" "/>
                <Title> /  </Title>
                <Content>
                  or    , <br />
                    .<br />
                ( ,      <br />
                 . )<br />

                <br /><br />
                       <br />
                    ,<br />
                 /   .<br />

                
                <br /><br />
                      , <br />
                / .<br />
                </Content>
            </ReviewModalWrap>
        </Container>
    )   
};

export default PolicyAlert;

const Container = styled.div`
    position: absolute;
    z-index: 99;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
`
const ReviewModalWrap = styled.div`
    background: #fff;
    width: 519px;
    height: 349px;
    padding: 35px 40px 45px 35px;
    box-sizing: border-box;
    border:1px solid #000;
    position: relative;
    ${Media(mediaPxMb,[
        `max-height: calc(100vh - ${Px(200, mbSize)})`,
        "overflow: scroll",
        Width(390, mbSize),
        Height(349, mbSize),
        Padding(35, 40, 45, 35, mbSize),
    ])}
`
const CloseBtn = styled.img`
    width: 22px;
    position: absolute;
    top:27px;
    right: 25px;
    cursor: pointer;
    ${Media(mediaPxMb,[ 
        Width(21, mbSize),
        Top(24, mbSize),
        Right(24, mbSize),
    ])}
`
const Title = styled.div`
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 11px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        MB(11, mbSize)
    ])}
`
const Content = styled.div`
    font-size: 15px;
    font-weight: 500;
    line-height: 30px;
    height: 234px;
    overflow: scroll;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
        LineH(30, mbSize),
        Height(234, mbSize),
    ])}
`