import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    FontSize,
    MB,
    Margin,
    Width,
    Height,
    Padding,
    MR,
    Px,
} from '../../../components/common/mediaStlye';

import DateText from '../../common/dateText';
import MyQnADetail from './myQnADetail';

const content = "    peg  ,      peg  ,      peg  ,      peg  ,  "
const title = ".   .";

const mediaPxMb = 1024;
const mbSize = 428;

const MyQnAListEl = ({item, setHeaderIcon, headerIcon}) => {
    const [sliceContent, setSliceContent] = useState("");
    const [isDetail, setIsDetail] = useState(false);
    const [detailData, setDetailData] = useState({});

    //     
    const returnType = (int) => {
        if(int == 0){
            return(" ")
        }else{
            return("")
        }
    }

    //  
    const onClickDetail = () => {
        setIsDetail(true);
        setDetailData({
            qustionData:new Date("2021-01-01"), //  
            title:title, //  
            type:1, //  
            prdImg:item.prdImg, //  
            prdType:"Skincare", //  
            prdName:"Soul", //  
            prdPrice:15800, //  
            question:content, //  
            answerDate:new Date("2021-01-01"), //  
            answerContent:content, //  
        })
    }


    // ,  --> 
    useEffect(() => {
        let newText = item.content;
        if(item.content.length > 37){
            newText=newText.substring(0, 37);
            setSliceContent(newText + "...")
            return;
        }
        setSliceContent(newText)
    }, [])

    useEffect(() => {
        if(isDetail){
            setHeaderIcon([...[false, true]])
        }else{
            setHeaderIcon([...[true, false]])
        }
    }, [isDetail])

    useEffect(() => {
        if(headerIcon[0]){
            setIsDetail(false);
        }
    }, [headerIcon])

    return(
        <ListWrap>
            {
                isDetail&&
                <MyQnADetail detailData={detailData} setIsDetail={setIsDetail}/>
            }
            <ContentWrap>
                <PrdImg src={item.prdImg}/>
                <TextWrap>
                    <DateType>
                        <QDate>{DateText(item.date, ".")}</QDate>
                        <Type active={item.type==1}>{returnType(item.type)}</Type>
                    </DateType>
                    <Name>{item.prdName}</Name>
                    <Title>{item.title}</Title>
                    <Content>{sliceContent}</Content>
                </TextWrap>
            </ContentWrap>
            <BtnWrap> 
                <ViewBtn onClick={() => onClickDetail()}></ViewBtn>
            </BtnWrap>
        </ListWrap>
    )

};

export default MyQnAListEl;

const ListWrap = styled.div`
    border-bottom: 1px solid #E2E2E2;
    padding: 15px 19px 0 60px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        Padding(15, 16, 15, 16, mbSize), 
    ])}
`
const ContentWrap = styled.div` 
    display: flex;
`
const PrdImg = styled.img` 
    width: 87px;
    height: 107px;
    margin-right: 13px;
    ${Media(mediaPxMb,[
        Width(87, mbSize),
        Height(107, mbSize),
        MR(8, mbSize),
    ])}
`
const TextWrap = styled.div` 
    width: calc(100% - 100px);
    ${Media(mediaPxMb,[
        `width:calc(100% - ${Px(95, mbSize)})`
    ])}
`
const DateType = styled.div` 
    display: flex;
    justify-content: space-between;
`
const QDate = styled.div` 
    font-size: 12px;
    color:#A0A0A0;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}
`
const Type = styled.div` 
    color:#A0A0A0;
    font-size: 12px;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}

    ${({active}) => {
        return active&&`color:#E83548;`
    }}
`
const Name = styled.div` 
    font-size: 20px;
    margin: 4px 0 5px ;
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
        Margin(4, 0, 5, 0, mbSize),
    ])}
`
const Title = styled.div` 
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 5px;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    width: 100%;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        MB(6, mbSize)
    ])}
`
const Content = styled.div` 
    font-size: 12px;
    font-weight: 300;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}
`
const BtnWrap = styled.div` 
    text-align: right;
    margin: 16px 0 15px;
    ${Media(mediaPxMb,[
    ])}
`
const ViewBtn = styled.button` 
    color:#fff;
    background-color: #211013;
    width: 98px;
    height: 33px;
    outline: none;
    border: 0;
    font-size: 12px;
    ${Media(mediaPxMb,[
        Width(98, mbSize),
        Height(33, mbSize),
        FontSize(12, mbSize)
    ])}
`
