import React, {useEffect, useState} from 'react';

// style
import styled from "styled-components"
import GalleryItemProduct from '../../../components/collection/galleryItemProduct';
import Header from '../../../components/header/header';

// img
import headImg from '../../../img/collection/episode/headImg.png';
import productImg_1 from '../../../img/collection/products/productImg_1.png';
import productImg_2 from '../../../img/collection/products/productImg_2.png';

import {
    FontSize,
    Height,
    LineH,
    Margin,
    Margin2,
    MB,
    Media,
    Padding2,
    PC,
    PT,
    Width,
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const content = "    .       ,    . ."

const SlowOrder = (props) => {
    const [isThree, setIsThree] = useState(false);
    const [listData, setListData] = useState([
        {img:productImg_2, type: "Skincare", name:" ", price:15800, desc:content},
        {img:productImg_1, type: "Fragrance", name:"  ", price:15800, desc:content},
        {img:productImg_2, type: "Skincare", name:" ", price:15800, desc:content},
        {img:productImg_1, type: "Fragrance", name:"  ", price:15800, desc:content},
        {img:productImg_2, type: "Skincare", name:" ", price:15800, desc:content},
        {img:productImg_1, type: "Fragrance", name:"  ", price:15800, desc:content},
        {img:productImg_2, type: "Skincare", name:" ", price:15800, desc:content},
        {img:productImg_1, type: "Fragrance", name:"  ", price:15800, desc:content},
    ])

    useEffect(() => {
        const newArr = listData;
        for(let i = 1 ; i < newArr.length ; i= i + 3){
            newArr[i].isMiddle = true;
        }
        setListData([...newArr]);
    }, [])


    return(
        <Container>         
            <Header isColumn isThree={isThree} setIsThree={setIsThree}/>

            <GridWrap>
                    <TopSection>
                        <TextWrap>
                            <HeadTitle>Main copy text place here</HeadTitle>
                            <Line></Line>
                            <HeadText>
                            , ,      .     <PC><br /></PC>
                              .<br />
                                       .<PC><br /></PC>
                                  .<br />
                            <PC>
                                             <br />
                                  .<br />
                                          <br />
                            </PC>
                            </HeadText>
                        </TextWrap>
                    </TopSection>

                
                <ListWrap>
                    <PC><ListTitle>Slow Order</ListTitle></PC>
                     <GalleryWrap>
                        {
                            listData.map((item, index) =>{
                                return(
                                    <GalleryItemProduct isMiddle={item.isMiddle} isThree={isThree} key={index} item={item} slow/>
                                )
                            })
                        }
                    </GalleryWrap>
                </ListWrap> 

            </GridWrap>
        </Container>
    )
};

export default SlowOrder;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
// 
const TopSection = styled.div`
    width: 100%;
    height: 565px;;
    background-image: url(${headImg});
    display: flex;
    align-items: center;
    justify-content: flex-end;
    ${Media(mediaPxTablet,[
        Height(565, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        Height(636, mbSize),
        "align-items: flex-end",
    ])}
`
const TextWrap = styled.div`
    width: 1110px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 60px;
    backdrop-filter: invert(0.9);
    ${Media(mediaPxMb,[
        "width:100%",
        Height(428, mbSize),
        Padding2(0, 37, mbSize),
    ])}
` 
const HeadTitle = styled.h1`
    font-family: "Big Caslon";
    font-size: 25px;
    font-weight: 500;
    color:#fff;
    ${Media(mediaPxTablet,[
        FontSize(25, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        FontSize(25, mbSize),
    ])}
` 
const Line = styled.span`
    margin: 24px 0;
    display: inline-block;
    width: 1px;
    height: 68px;
    border-left: 1px solid #fff;
    ${Media(mediaPxTablet,[
        Height(68, mediaPxTablet),
        Margin2(24, 0, mediaPxTablet),
        Height(68, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Height(68, mbSize ),
        Margin(27, 0, 19, 0, mbSize)
    ])}
`
const HeadText = styled.p`
    font-weight: 500;
    font-size: 15px;
    line-height: 30px;
    color:#fff;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
        LineH(30, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
        LineH(30, mbSize),
        "font-weight:300", 
    ])}
`
// 
const ListWrap = styled.div`
    width:1052px;
    margin:0 auto;
    margin-bottom: 219px;
    ${Media(mediaPxTablet,[
        Width(1052, mediaPxTablet),
        MB(219, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "width:100%",
        "margin:0",
        MB(273, mbSize)
    ])}
`
const ListTitle = styled.h2`
    padding:132px 0 112px;
    font-size: 35px;
    font-weight: 500;
    font-family: "Big Caslon";
`
const GalleryWrap = styled.div`
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
    margin-bottom: 320px;
    ${Media(mediaPxTablet,[
        MB(320, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        MB(273, mbSize),
        "justify-content: unset;",
    ])}
`