

//React
import React, { useEffect } from 'react';
import { BrowserRouter as Router, Route} from "react-router-dom";

// Css
import './App.css';

// Components

// Pages
import Main from './pages/main/main';
import Info from './pages/info/info';
import Taste from './pages/taste/taste';
import TasteDetailFirst from './pages/taste/tasteDetailFirst';
import TasteDetailSecond from './pages/taste/tasteDetailSecond';
import Login from './pages/login/login';
import MSeulmit from './pages/taste/mSeulmit/mSeulmit';
import MSeulmitDetailFirst from './pages/taste/mSeulmit/mSeulmitDetailFirst';
import Episode from './pages/collection/episode/episode';
import EpisodeDetail from './pages/collection/episode/episodeDetail';
import Product from './pages/collection/products/product';
import ProductDetail from './pages/collection/products/productDetail';
import NoticeDetail from './pages/info/notice/noticeDetail';
import Review from './pages/info/review/review';
import Purchase from './pages/purchase/purchase';
import SlowOrder from './pages/Inside/sloworder/slowOrder';
import MyPage from './pages/myPage/myPage';
import SaleProduct from './pages/collection/products/saleProduct';
import ChangePw from './pages/login/changePw';


function App() {

  /*
    --    --
       ?                  ------  - api 
    ->   

        (     )                   ------ 
    ->  ->   

    Notice, Artist     


    --  --
     - , 
    Brand 
     .
      
       
      
    
    --  --
    
    sold out  

     
    ID ""   .
  */

  return (
    <Router>
      {/* Main */}
      <Route exact path="/" component={() => <Main />}/>
      
      {/* Login */}
      <Route exact path="/Login" component={() => <Login />}/>
      <Route exact path="/ChangePassword" component={() => <ChangePw />}/>
      
      {/* MyPage */}
      <Route exact path="/MyPage" component={() => <MyPage />}/>

      {/* Inside */}
      <Route exact path="/SlowOrder" component={() => <SlowOrder />}/>  

      {/* Info - Notice*/}
      <Route exact path="/Info" component={() => <Info />}/>
      <Route exact path="/NoticeDetail" component={() => <NoticeDetail />}/>

      {/* Info - Review*/}
      <Route exact path="/Review" component={() => <Review />}/>

      {/* Taste - Artist*/}
      <Route exact path="/Taste" component={() => <Taste />}/>
      <Route exact path="/TasteDetailFirst" component={() => <TasteDetailFirst />}/>
      <Route exact path="/TasteDetailSecond" component={() => <TasteDetailSecond />}/>

      {/* Taste - M.seulmit*/}
      <Route exact path="/MSeulmit" component={() => <MSeulmit />}/>
      <Route exact path="/MSeulmitDetailFirst" component={() => <MSeulmitDetailFirst />}/>
      
      {/* Collection - Episode */} 
      <Route exact path="/Episode" component={() => <Episode />}/>
      <Route exact path="/EpisodeDetail" component={() => <EpisodeDetail />}/>

      {/* Collection - Product */} 
      <Route exact path="/Product" component={() => <Product />}/>
      <Route exact path="/SaleProduct" component={() => <SaleProduct />}/>
      <Route exact path="/ProductDetail/:text" component={(route) => {return(<ProductDetail status={route.match.params.text} />)}}/>
      
      {/* Purchase */}
      <Route exact path="/Purchase/:text" component={(route) => {return(<Purchase status={route.match.params.text} />)}}/>
    </Router>
  );
}

export default App;
